import React, { Component } from 'react';

class Register extends Component {
    constructor() {
        super();
        this.state = {
            registerName: '',
            registerEmail: '',
            registerPassword: '',
            error: ''
        }
    }

    onNameChange = (event) => {
        this.setState({
            registerName: event.target.value,
            error: ''
        })
    }

    onEmailChange = (event) => {
        this.setState({
            registerEmail: event.target.value,
            error: ''
        })
    }

    onPasswordChange = (event) => {
        this.setState({
            registerPassword: event.target.value,
            error: ''
        })
    }

    validateEmail = (email)  => {
        var re = /\S+@\S+\.\S+/;
        return re.test(email);
    }

    onSubmitRegister = () => {
        if(!this.state.registerName)
        {
            this.setState({ error: 'Name is required.'});
            return;
        }

        if(!this.state.registerEmail)
        {
            this.setState({ error: 'Email is required.'});
            return;
        }

        if(!this.state.registerPassword)
        {
            this.setState({ error: 'Password is required.'});
            return;
        }

        if(!this.validateEmail(this.state.registerEmail))
        {
            this.setState({ error: 'Enter a valid emailaddress.'});
            return;
        }

        if(this.state.registerPassword.length < 6)
        {
            this.setState({ error: 'Enter a password with at least 6 characters.'});
            return;
        }

        fetch('https://gentle-beach-71242.herokuapp.com/register', {
            method: 'post',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({
                name: this.state.registerName,
                email: this.state.registerEmail,
                password: this.state.registerPassword
            })
        })
        .then(response => response.json())
        .then(user => {
            if (user.name && user.name === this.state.registerName)
            {
                this.props.loadUser(user);
                this.props.onRouteChange('home');
            }
            else
            {
                this.setState({ error: 'Registration failed.'});
            }
        })
        .catch(err => {
            console.log(err);
        })
    }

    render() {
        return (
            <article className="br3 ba b--black-10 mv4 w-100 w-50-m w-25-l mw6 shadow-5 center">
                <main className="pa4 black-80">
                <div className="measure">
                    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
                        <legend className="f1 fw6 ph0 mh0">Register</legend>
                        <div className="mt3">
                            <label className="db fw6 lh-copy f6" htmlFor="name">Name</label>
                            <input className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="text" name="name"  id="name" onChange={this.onNameChange}/>
                        </div>
                        <div className="mt3">
                            <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
                            <input className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="email" name="email-address"  id="email-address" onChange={this.onEmailChange}/>
                        </div>
                        <div className="mv3">
                            <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
                            <input className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="password"  id="password" onChange={this.onPasswordChange}/>
                        </div>
                    </fieldset>
                    <div className="">
                        <input 
                            className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" 
                            type="submit" 
                            value="Register"
                            onClick={this.onSubmitRegister}
                        />
                        <div className='red f5 mt3'>
                            {this.state.error}
                        </div>
                    </div>
                </div>
                </main>
            </article>
        );
    }
}

export default Register;